
      import initServer from 'next-plugin-loader?middleware=on-init-server!'
      import onError from 'next-plugin-loader?middleware=on-error-server!'
      
      const runtimeConfig = {}
      
      const { parse } = require('url')
      const { apiResolver } = require('next/dist/next-server/server/api-utils')
      
    const { rewrites } = require('private-dot-next/routes-manifest.json')
    const { pathToRegexp, default: pathMatch } = require('next/dist/next-server/server/lib/path-match')
  

      

      
    const getCustomRouteMatcher = pathMatch(true)
    const {prepareDestination} = require('next/dist/next-server/server/router')

    function handleRewrites(parsedUrl) {
      for (const rewrite of rewrites) {
        const matcher = getCustomRouteMatcher(rewrite.source)
        const params = matcher(parsedUrl.pathname)

        if (params) {
          const { parsedDestination } = prepareDestination(
            rewrite.destination,
            params,
            parsedUrl.query
          )
          Object.assign(parsedUrl.query, parsedDestination.query, params)
          delete parsedDestination.query

          Object.assign(parsedUrl, parsedDestination)

          if (parsedUrl.pathname === '/api/user'){
            break
          }
          
        }
      }

      return parsedUrl
    }
  

      export default async (req, res) => {
        try {
          await initServer()

          
          const parsedUrl = handleRewrites(parse(req.url, true))

          const params = {}

          const resolver = require('private-next-pages/api/user.js')
          await apiResolver(
            req,
            res,
            Object.assign({}, parsedUrl.query, params ),
            resolver,
            {previewModeId:"50326dc7246e78629b2d7e40b8818c8b",previewModeSigningKey:"dcba1bf79357ef64267c0eb3884fc38ebbf3db318053fb10d5790da32eba96ac",previewModeEncryptionKey:"84ec1c12ee04b46c6c3d27fe56d6affae91b6cc341deff2f3350a6cabbb38e52"},
            onError
          )
        } catch (err) {
          console.error(err)
          await onError(err)

          if (err.code === 'DECODE_FAILED') {
            res.statusCode = 400
            res.end('Bad Request')
          } else {
            res.statusCode = 500
            res.end('Internal Server Error')
          }
        }
      }
    